:root {
    --zoom: 1;
}

/* External styling */
.generate-button {
  position: absolute;
  z-index: 20;
  margin-top: 20px;
  margin-left: 20px;
	line-height: 50px;
	height: 50px;
	text-align: center;
	width: 250px;
  cursor: pointer;
  color: #FFF;
	transition: all 0.3s;
}

.generate-button span {
	transition: all 0.3s;
}

.generate-button::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 1px;
	border-bottom-width: 1px;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: rgba(255,255,255,0.5);
	border-bottom-color: rgba(255,255,255,0.5);
	transform: scale(0.1, 1);
}

.generate-button:hover span {
	letter-spacing: 2px;
}

.generate-button:hover::before {
	opacity: 1;	
	transform: scale(1, 1);	
}

.generate-button::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.3s;
  background-color: rgba(255,255,255,0.1);
  opacity: 0.5;
  border-left: 1px solid white;
  border-right: 1px solid white;
}

.generate-button:hover::after {
	opacity: 0;	
	transform: scale(0.1, 1);
}
/* End of external styling */

.space-background {
    background-color: black;
    height: 100%;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    overflow: hidden;
    zoom: var(--zoom);
}